import React from 'react';
import styles from './ProjectTable.module.css';

const ProjectTable = ({ projects, onRate, isLoggedIn }) => (
  <table className={styles.table}>
    <thead>
      <tr>
        <th>#</th>
        <th>Datum</th>
        <th>Nzev</th>
        <th>Popis problmu</th>
        <th>Popis een</th>
        {isLoggedIn && <th>Akce</th>}
      </tr>
    </thead>
    <tbody>
      {projects.length > 0 ? (
        projects.map((project) => (
          <tr key={project.row_number || Math.random()}>
            <td>{project.row_number || '-'}</td>
            <td>{project["asov znaka"] || '-'}</td>
            <td>{project["Nzev"] || '-'}</td>
            <td>{project["Popis problmu"] || '-'}</td>
            <td>{project["Popis een"] || '-'}</td>
            {isLoggedIn && (
              <td>
                <button onClick={() => onRate(project)}>Hodnotit</button>
              </td>
            )}
          </tr>
        ))
      ) : (
        <tr>
          <td colSpan={isLoggedIn ? 6 : 5}>dn npady k zobrazen</td>
        </tr>
      )}
    </tbody>
  </table>
);

export default ProjectTable;
