import * as Yup from 'yup';

export const validationSchema = (emojiLimit) => Yup.object({
  emojis: Yup.string()
    .required('Hodnocen je povinn')
    .max(emojiLimit, `Maximln poet emotikon je ${emojiLimit}`),
  name: Yup.string().required('Jmno je povinn'),
  projectId: Yup.string().required()
});
